@charset "UTF-8";
/* stylelint-disable */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  /* Make type rendering look crisper */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Disable auto-enlargement of small text in Safari */
  text-size-adjust: 100%;
  /* Enable kerning and optional ligatures */
  text-rendering: optimizeLegibility;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  font-size: 16px;
  color: #212529;
  padding: 0;
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  border: 0;
}

textarea {
  height: auto;
  min-height: 50px;
  overflow: auto;
}

select {
  width: 100%;
}

button {
  border: 0;
  padding: 0;
  line-height: 1;
  overflow: visible;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  cursor: pointer;
  text-decoration: none;
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

b,
strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
  color: inherit;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  cursor: pointer;
  appearance: button;
}

button[disabled],
html input[disabled] {
  cursor: not-allowed;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}

:focus {
  outline: none;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  box-sizing: content-box;
  appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  appearance: none;
}

legend {
  padding: 0;
  border: 0;
}

optgroup {
  font-weight: 700;
}

ul,
ol,
dl,
dd {
  margin: 0;
  padding: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

/* stylelint-enable */
.c-action-panel {
  top: 0;
  bottom: 0;
  display: flex;
  position: fixed;
  flex-direction: column;
  width: 320px;
  right: -320px;
  backface-visibility: hidden;
  z-index: 1900;
  transition: 0.3s ease;
  border-left: 1px solid #e9ecef;
  background-color: #f8f9fa;
}

.c-action-panel-left {
  border-right: 1px solid #e9ecef;
  box-shadow: none;
  border-left: 0;
}

.c-action-panel-body {
  flex: 1;
}

.c-action-panel-header {
  border-bottom: 1px solid #e9ecef;
  background-color: #ffffff;
  padding: 12px 16px;
}

.c-action-panel-header-title {
  display: inline-block;
  color: #212529;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
}

.c-action-panel-header-subtitle {
  display: block;
  font-size: 12px;
  line-height: 12px;
}

.c-action-panel-header-subtitle + .c-action-panel-header-menu {
  height: 60px;
}

.c-action-panel-header-menu {
  top: 0;
  float: right;
  display: flex;
  position: absolute;
  align-items: center;
  right: 16px;
  height: 48px;
}

.c-action-panel-header-menu > * {
  margin: auto 12px;
}

.c-action-panel-header-menu > *:last-child {
  margin-right: 0;
}

.c-action-panel-header-menu .c-btn {
  margin: auto 4px;
}

.c-action-panel-header-menu .c-btn:last-child {
  margin-right: 0;
}

.c-action-panel-header-menu-item {
  padding-left: 4px;
}

.c-action-panel-header-menu-icon {
  font-size: 16px;
  color: #adb5bd;
}

.c-action-panel-header-menu-icon:hover {
  color: #c9cfd4;
}

.c-action-panel-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border-top: 1px solid #e9ecef;
  background-color: #ffffff;
  padding: 12px 16px;
}

.c-action-panel-footer-link {
  text-decoration: underline;
  color: #1b6ec2;
  font-size: 12px;
  line-height: 16px;
}

.c-action-panel-footer-link:hover {
  color: #228ae6;
}

.c-action-panel-footer-item {
  display: flex;
  flex: 1 1 auto;
  align-items: center;
  font-size: 12px;
  line-height: 16px;
}

.c-action-panel-footer-item-center {
  justify-content: center;
}

.c-action-panel-footer-item-right {
  justify-content: flex-end;
}

.c-action-panel-footer-item-left {
  justify-content: flex-start;
}

.c-action-panel-close {
  border: 0;
  cursor: pointer;
  pointer-events: auto;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  height: 28px;
  width: 28px;
  outline: none;
  position: relative;
  vertical-align: top;
  text-decoration: none;
}

.c-action-panel-close::before {
  height: 3px;
  width: 14px;
}

.c-action-panel-close::after {
  height: 14px;
  width: 3px;
}

.c-action-panel-close::before, .c-action-panel-close::after {
  position: absolute;
  display: block;
  background-color: #868e96;
  border-radius: 2px;
  content: "";
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform-origin: center center;
}

.c-action-panel-close:hover::before, .c-action-panel-close:hover::after {
  background-color: #6c757d;
}

.c-action-panel-push {
  position: relative;
  display: none;
}

.c-action-panel-lg {
  width: 400px;
  right: -400px;
}

.c-action-panel.c-action-panel-visible {
  right: 0;
  box-shadow: 0 8px 8px -2px rgba(33, 37, 41, 0.1);
}

.c-action-panel-backdrop {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  position: fixed;
  transition: 0.3s ease;
  background-color: rgba(0, 0, 0, 0.75);
}

.c-action-panel-backdrop-transparent {
  background-color: transparent;
}

.c-action-panel-visible.c-action-panel {
  right: 0;
  left: auto;
  display: flex;
}

.c-action-panel-visible.c-action-panel-push {
  right: 0;
  left: auto;
  display: flex;
}

.c-notifications .c-media-list .c-media {
  margin: 8px;
  box-shadow: 0 2px 4px -2px rgba(33, 37, 41, 0.1);
}

.c-notifications .c-media-list .c-media:hover {
  background: #ffffff !important;
  box-shadow: 0 4px 4px -2px rgba(33, 37, 41, 0.1);
}

.c-notifications .c-media-list .c-media-read {
  box-shadow: none;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
}

.c-notifications .c-media-list .c-media-read:hover {
  box-shadow: none;
  background: #f8f9fa !important;
}

.c-avatar {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  box-shadow: none;
  text-align: center;
  position: relative;
  color: #ffffff;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  background: #495057;
  font-size: 12px;
  font-weight: 600;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  border-radius: 200px;
}

.c-avatar i {
  font-size: 16px;
}

.c-avatar img {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: relative;
  border: none;
  border-radius: 200px;
}

.c-avatar-square {
  border-radius: 2px;
}

.c-avatar-square img {
  border-radius: 2px;
}

.c-avatar-square.c-avatar[data-status]::after {
  top: -2px;
  right: -2px;
}

.c-avatar[data-status]::after {
  content: '';
  top: 0;
  right: 0;
  z-index: 1;
  height: 8px;
  min-width: 8px;
  position: absolute;
  color: #ffffff;
  box-shadow: 0 0 0 1px #ffffff;
  background-clip: padding-box;
  border-radius: 50px;
  font-weight: 600;
}

.c-avatar[data-badge]::after {
  z-index: 1;
  font-size: 0;
  white-space: nowrap;
  content: attr(data-badge);
}

.c-avatar[data-text]::before {
  top: 50%;
  left: 50%;
  z-index: 0;
  position: absolute;
  color: currentColor;
  content: attr(data-text);
  transform: translate(-50%, -50%);
}

.c-avatar-primary {
  background-color: #1b6ec2;
}

.c-avatar[data-status='primary']::after {
  background-color: #1b6ec2;
}

.c-avatar-success {
