.c-alert {
  color: #212529;
  display: flex;
  text-align: left;
  position: relative;
  background-color: rgba(27, 110, 194, 0.07);
  border: 1px solid;
  margin: 0 0 16px 0;
  padding: 8px;
  align-items: flex-start;
  font-size: 12px;
  border-radius: 2px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 16px;
}

.c-alert-close {
  position: relative;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  width: 12px;
  height: 12px;
  overflow: hidden;
  order: 2;
  margin-left: auto;
}

.c-alert-close:hover {
  cursor: pointer;
}

.c-alert-close:hover::before, .c-alert-close:hover::after {
  background: #383f45;
}

.c-alert-close::before, .c-alert-close::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  background: #212529;
}

.c-alert-close::before {
  transform: rotate(45deg);
}

.c-alert-close::after {
  transform: rotate(-45deg);
}

.c-alert-title {
