.c-skeleton-loading {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 2px;
  height: 8px;
  background: #dee2e6;
  background-image: linear-gradient(140deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 70% 40px;
  background-position: -200% 0;
  background-repeat: no-repeat;
  animation: skeletonIteration 2s infinite;
  margin: 24px 0;
}

.c-skeleton-loading::after {
  position: absolute;
  top: -16px;
  left: 0;
  right: 5%;
  border-radius: 2px;
  height: 8px;
  background: #dee2e6;
  background-image: linear-gradient(140deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 70% 40px;
  background-position: -200% 0;
  background-repeat: no-repeat;
  animation: skeletonIteration 2s infinite;
  content: '';
}

.c-skeleton-loading::before {
  position: absolute;
  top: 16px;
  left: 0;
  right: 10%;
  border-radius: 2px;
  height: 8px;
  background: #dee2e6;
  background-image: linear-gradient(140deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 70% 40px;
  background-position: -200% 0;
  background-repeat: no-repeat;
  animation: skeletonIteration 2s infinite;
  content: '';
}

.c-skeleton-bar {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 2px;
  height: 8px;
  background: #dee2e6;
  background-image: linear-gradient(140deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0) 100%);
  background-size: 70% 40px;
  background-position: -200% 0;
  background-repeat: no-repeat;
  animation: skeletonIteration 2s infinite;
  margin: 8px 0;
}

.c-loader {
  width: 32px;
  height: 32px;
  border: 4px solid #dee2e6;
  border-top-color: #1b6ec2;
  border-radius: 50px;
  animation: loaderRotate 0.71s linear infinite;
  margin: 0 auto;
}

@keyframes loaderRotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.c-loader-backdrop {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1800;
  visibility: hidden;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.75);
}

.c-loader-backdrop .c-loader {
  opacity: 0;
  visibility: hidden;
}

.c-loader-backdrop.c-loader-visible {
  opacity: 1;
  visibility: visible;
}

.c-loader-backdrop.c-loader-visible .c-loader {
  visibility: visible;
  transform: scale(1);
  opacity: 1;
}

